/* Estilize a seção "Sobre" */
.sobre {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    /* background-color: blue; */
    color: #FFF;
}

/* Estilize o título */
/* .sobre h2 {
    font-size: 24px;
    margin-bottom: 20px;
} */

/* Estilize o contêiner do conteúdo "Sobre" */
.conteudo-sobre {
    flex-direction: row;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px;
}

/* Estilize a foto */
.foto img {
    border-radius: 50%;
    box-shadow: rgba(255, 255, 255, 0.75) 0px 5px 15px;
}

/* Estilize o texto */
.texto {
    padding: 50px;
    width: 30%;
    /* text-align: left; */
    align-items: center;
}

/* Quebrar de linha do texto em telas menores */
@media (max-width: 768px) {
    .conteudo-sobre {
        flex-direction: column;
    }

    .foto img {
        margin-right: 0;
    }
}

.skills {
    font-size: 24px;
