.containerS {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    max-width: 1200px;
    padding: 100px 0 50px;
}

.cardS {
    position: relative;
    min-width: 320px;
    height: 350px;
    box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.2), inset -5px -5px 15px rgba(255, 255, 255, 0.1),
        5px 5px 15px rgba(0, 0, 0, 0.3), -5px -5px 15px rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    margin: 30px;
    flex-direction: column;
}

.cardS .box {
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
    bottom: 20px;
    background: #2a2b2f;
    border-radius: 15px;
    border: 2px solid #1e1f23;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: transform 0.4s, box-shadow 0.4s;
}

.cardS:hover .box {
    transform: translateY(-50px);
    box-shadow: 0 40px 70px rgba(0, 0, 0, 0.5);
}

.contentS {
    padding: 20px;
    text-align: center;
    flex-direction: column;
}

.icon {
    position: relative;
    width: 80px;
    height: 80px;
    background: #f00;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    margin-bottom: 10px;
}

.icon .fa {
    color: #fff;
    font-size: 2em;
}

.contentS h3 {
    /* font-size: 1.8em; */
    color: #fff;
    font-weight: 300;
}

.contentS h4 {
    color: #fff;
    font-size: 3em;
    font-weight: 700;
}

.contentS h4 sup {
    font-size: 0.75em;
    font-weight: 300;
}

.contentS ul {
    position: relative;
}

.contentS ul li {
    list-style: none;
    color: #fff;
    margin: 5px 0;
}

.fa-check {
    color: #0f0;
    margin-right: 10px;
}

.fa-times {
    color: #f00;
    margin-right: 10px;
}

